<template>
  <section class="section">
    <div class="container is-fluid">
        <div class="columns">
          <div class="column is-5">
            <h1 class="title is-4">My Websites</h1>
            <h2 class="subtitle is-6">you have 0 websites</h2>

            <div class="columns is-multiline">
              <div class="column is-6" v-for="card in cards">   
                <div class="card">
                  <div class="card-image">
                    <figure class="image is-4by3">
                      <img src="http://bulma.io/images/placeholders/1280x960.png" alt="Image">
                    </figure>
                  </div>
                  <div class="card-content">
                    <div class="content">
                      Website name
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="column is-offset-1">
            <h1 class="title is-4">Create a new website</h1>
            <h2 class="subtitle is-6">choose a theme to start :)</h2>
            
            <div class="columns is-multiline">
              <div class="column is-3" v-for="card in cards">   
                <div class="card">
                  <div class="card-image">
                    <figure class="image is-4by3">
                      <img src="http://bulma.io/images/placeholders/1280x960.png" alt="Image">
                    </figure>
                  </div>
                  <div class="card-content">
                    <div class="content">
                      Website name
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'home',

  data () {
    return {
      cards: [1,2]
    }
  },

  created () {
    builder.iframe.isReady = false;
    console.log('myWebsites page started');
  }
}
</script>

<style scoped>
  .card-content {
    padding: 10px;
  }
</style>



